<template>
    <div id="view" v-if="isLogin">
        <el-dialog title="添加用户" :visible.sync="showInfoDialog" width="350px">
            <el-form label-position="left" label-width="100px" :model="adminInfo">
                <el-form-item label="ID">
                    <el-input disabled :placeholder="adminInfo.id"></el-input>
                </el-form-item>
                <el-form-item label="身份">
                    <el-input disabled :placeholder="adminInfo.role"></el-input>
                </el-form-item>
                <el-form-item label="用户名">
                    <el-input disabled :placeholder="adminInfo.username"></el-input>
                </el-form-item>
                <el-form-item label="昵称">
                    <el-input v-model="adminInfo.nickname" tabindex="1"></el-input>
                </el-form-item>
                <el-form-item label="邮箱">
                    <el-input v-model="adminInfo.email" tabindex="2"></el-input>
                </el-form-item>
                <el-form-item label="手机">
                    <el-input v-model="adminInfo.phone" tabindex="3"></el-input>
                </el-form-item>
                <el-form-item label="上次登陆时间">
                    <el-input disabled :placeholder="adminInfo.last_login_time"></el-input>
                </el-form-item>
                <el-form-item label="上次登陆IP" v-if="adminInfo.last_login_ip">
                    <el-input disabled :placeholder="adminInfo.last_login_ip"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="showInfoDialog = false">取 消</el-button>
                <el-button type="primary" :disabled="true">确 定</el-button>
            </div>
        </el-dialog>
        <keep-alive>
            <el-header :style="globalHeaderStyle">
                <img id="logo" src="../assets/logo.png" style="height: 100%" />
                <div :style="loginUserStyle">
                    <el-dropdown @command="handleCommand">
                        <i class="el-icon-s-home" style="margin-right: 15px"></i>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="detail">查看详情</el-dropdown-item>
                            <el-dropdown-item>新增</el-dropdown-item>
                            <el-dropdown-item command="logout">注销登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <span>{{ adminInfo.nickname }}</span>
                </div>
            </el-header>
        </keep-alive>
        <el-container style="height: 95%; border: 1px solid #eee">
            <keep-alive>
                <el-aside width="150px" style="background-color: rgb(238, 241, 246);color: #333">
                    <el-menu
                        :default-active="pathChain[pathChain.length-1].path"
                        class="el-menu-vertical-demo"
                        @open="handleOpen"
                        @close="handleClose"
                        style="width: 100%;overflow: hidden;"
                        active-text-color="#2792C3"
                        unique-opened
                        @select="handleSelect"
                    >
                        <el-submenu
                            v-for="(menuGroup,route) in menuConfig"
                            :index="route"
                            :key="route"
                        >
                            <template slot="title">
                                <i :class="menuGroup.icon"></i>
                                {{ menuGroup.name }}
                            </template>
                            <el-menu-item-group
                                v-for="(frame,route) in menuGroup.frames"
                                :key="route"
                            >
                                <el-menu-item
                                    :index="route"
                                    :disabled="frame.disable"
                                >{{ frame.name }}</el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                    <footer :style="footerStyle">
                        2020©️主音
                        <br />All rights reserved.
                    </footer>
                </el-aside>
            </keep-alive>
            <el-container style="overflow-x: hidden">
                <el-header :style="breadcrumbHeaderStyle">
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item>后台管理</el-breadcrumb-item>
                        <el-breadcrumb-item
                            v-for="index in pathChain"
                            :key="index.path"
                        >{{ index.name }}</el-breadcrumb-item>
                    </el-breadcrumb>
                </el-header>

                <router-view />
            </el-container>
        </el-container>
    </div>
</template>

<script>
import { getToken } from '@/utils/auth'
import MenuConfig from '@/config/sidemenu'
import { mapGetters } from 'vuex'
import Consts from '@/config/const'

// const initAdminInfoForm = () => {
//   return {
//     nickname: this.adminInfo.nickname,
//     email: this.adminInfo.email,
//     phone: this.adminInfo.phone
//   }
// }

export default {
    name: 'page-view',

    created: function() {
        // 初始化路由链
        var routeArray = this.$route.path.split('/').splice(1)
        this.updatePathChain(routeArray)
        // 检查登录状态
        if (!getToken()) {
            this.$router.push({
                path: '/login'
            })
        } else {
            this.isLogin = true
            this.$store
                .dispatch('Home/getInfo')
                .then(response => {
                    console.log(response.msg)
                })
                .catch(error => {
                    console.log(error)
                })
        }
    },
    watch: {
        // 监听路由变化
        $route(to, from) {
            var routeArray = to.path.split('/').splice(1)
            this.updatePathChain(routeArray)
            // console.log('触发watch')
        }
    },
    computed: {
        ...mapGetters({
            adminInfo: 'adminInfo'
        })
    },
    methods: {
        handleSelect(index, indexPath) {
            // console.log('触发select')
            // 跳转路由
            this.$router.push(this.updatePathChain(indexPath))
        },
        handleOpen(key, keyPath) {
            // console.log(key, keyPath)
            //   this.currentMenu = key
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath)
        },
        handleCommand(command) {
            console.log(command)
            if (command === 'logout') {
                this.$store
                    .dispatch('Home/logout')
                    .then(response => {
                        location.reload()
                        console.log(response.msg)
                    })
                    .catch(error => {
                        console.log(error)
                    })
            } else if (command === 'detail') {
                this.showInfoDialog = true
            }
        },
        updatePathChain(indexPath = []) {
            if (indexPath === []) {
                return
            }
            // console.log(this.indexPath)
            // 清空原链
            this.pathChain.length = 0
            // 路由表达式
            var go = ''
            // 导入导航栏配置信息
            var menuConfig = this.menuConfig
            // 路径遍历
            indexPath.forEach(path => {
                if (!menuConfig) {
                    return
                }
                go += '/' + path
                var tmp = { path: path, name: menuConfig[path].name }
                this.pathChain.push(tmp)
                menuConfig = menuConfig[path].frames || {}
                // console.log(menuConfig)
            })
            return go
        }
        // initAdminInfoForm () {
        //   console.log(this.adminInfo)
        //   return {
        //     nickname: this.adminInfo.nickname,
        //     email: this.adminInfo.email,
        //     phone: this.adminInfo.phone
        //   }
        // }
    },
    data() {
        return {
            isLogin: false,
            showInfoDialog: false,
            // 用于面包屑的的路径链
            pathChain: [],
            Consts: Consts,
            menuConfig: MenuConfig,
            globalHeaderStyle: {
                height: '5%',
                display: 'flex',
                alignItems: 'center'
            },
            breadcrumbHeaderStyle: {
                display: 'flex',
                alignItems: 'center',
                backgroundColor: '#fff',
                color: '#333',
                lineHeight: '60px',
                minWidth: '300px'
            },
            footerStyle: {
                position: 'absolute',
                width: '150px',
                textAlign: 'center',
                bottom: '0px',
                fontSize: '10px'
            },
            loginUserStyle: {
                right: '10px',
                textAlign: 'right',
                fontSize: '12px',
                position: 'absolute'
            }
        }
    }
}
</script>



